import { endOfDay, format } from "date-fns";
import startOfDay from "date-fns/startOfDay";
import { computed, makeAutoObservable, runInAction } from 'mobx';
import { db, storage } from "../firebase/config";
import TransactionDetail from '../models/TransactionDetail';

class MoneyStore {  

  loading: boolean = true;
  balance = null;
  transToday: [TransactionDetail] = [];

  constructor (){
    console.log("Initialize CommonStore...");
    makeAutoObservable(this);
  }

  loadBalanceAndTrans = async(user,datetime) => {
    await this.loadBalance(user);
    await this.loadTrans([user],datetime);
  }

  loadBalance = async (user) => {
    this.loading = true;
    const userDoc = await db.collection("money").doc(`${user}_balance`).get();
    if (userDoc.exists){
      runInAction(()=>{
        this.balance = userDoc.data();
      })
    } else {
      this.balance = {
        total: 0,
        lastUpdate: new Date(),
        target: 20000000
      };
      await db.collection("money").doc(`${user}_balance`).set(this.balance);
    }
    runInAction(()=>{
      this.loading = false;
    })
  }

  @computed get completedPercent(){
    return this.balance.total/this.balance.target*100;
  }

  addTrans = async (user,transDetail: TransactionDetail) => {
    this.loading = true;
    const date = format(transDetail.datetime, "dd-MM-yyyy");

    // save transaction
    // commit to balance
    const val = parseInt(transDetail.value);
    this.balance.total += isNaN(val) ? 0: val;
    transDetail.total = this.balance.total;

    await db.collection("money").doc(`${user}_${date}_${transDetail.id}`).set(transDetail);

    await db.collection("money").doc(`${user}_balance`).set({
      total: this.balance.total,
      lastUpdate: new Date(),
      target: this.balance.target?? 20000000
    });

    runInAction(()=>{
      this.transToday.push(transDetail);
      this.loading = false;
    })
  }

  loadTrans = async (users,datetime) => {
    this.loading = true;
    this.transToday = [];

    const fromDate = startOfDay(datetime);
    const toDate = endOfDay(datetime);
    const moneyTrans = await db.collection("money")
      .where("user", "in", users)
      .where("datetime", ">=", fromDate)
      .where("datetime", "<=", toDate).get();
      moneyTrans.forEach(trans => {
        this.transToday.push(trans.data());
      });
      runInAction(()=>{
        this.loading = false;
      })
  }

}

export default MoneyStore;
