import { FontAwesome5 } from '@expo/vector-icons';
import { format } from "date-fns";
import { Button, List, ListItem, Spinner, View } from "native-base";
import { Text } from "native-base";
import React, { useState, useEffect } from "react";
import { StyleSheet } from "react-native";
import { TextInput } from 'react-native-gesture-handler';
import { useStores } from "../stores/RootStore";
import TransactionDetail from '../models/TransactionDetail';
import ProgressBar from './ProgressBar';
import EvalDetail from './EvalDetail';
import { toJS } from 'mobx';
import { observer } from 'mobx-react-lite';

const EvalResults = (props) => {
    
    const {commonStore, moneyStore} = useStores();

    useEffect(()=>{
        moneyStore.loadBalanceAndTrans(props.user,props.date);
    },[props.user,props.date, props.tabIndex]);

    const submitTranDetail = (transDetail: TransactionDetail) => {
        moneyStore.addTrans(props.user,toJS(transDetail));
    }

    const renderAward = (tran: TransactionDetail) => {
        return (
            <View style={{flexDirection: 'column', margin: 10}}>
                <FontAwesome5 name={tran.awardName} style={{fontSize:tran.size}} color={tran.value > 0 ? "red": "#786d6c"} />
                <Text> {(tran.value/1000).toFixed(0)}k </Text>
                { tran.comments.length > 0 && tran.comments[0] != "" &&
                  <Text style={{ marginTop:10, fontStyle: "italic", color: "#3962a3"}}> {format(new Date(tran.datetime*1000),'HH:mm')}: {tran.comments[0]}</Text>
                }
            </View>
        )
    }

    return moneyStore.loading && !moneyStore.balance ? (<Spinner color="green" />) : (
        <View style={{ flexDirection: "column", width: "100%"}}>
          <View style={{}}>
              <Text style={{color: "#1d4d99"}}> Total balance: {(moneyStore.balance.total/1000).toFixed(0)}k/{(moneyStore.balance.target/1000).toFixed(0)}k</Text>
              <ProgressBar bgcolor={'#eb4034'} completed={moneyStore.completedPercent.toFixed(2)} ></ProgressBar>
          </View>
          <View>
            <Text style={{color: "#1d4d99"}}> Results: </Text>
            <View style={{flexDirection: "column"}}>
            {
                moneyStore.transToday.map((tran) => (
                    renderAward(tran)
                ))
            }
            </View>
          </View>
          {commonStore.userRole == 'admin' &&
            <View style={{flexDirection: "row", marginTop: 20}}>
                <Text style={{color: "#1d4d99"}}> Evaluation: </Text>
                <EvalDetail user={props.user} date={props.date} onSubmitTranDetail={submitTranDetail}></EvalDetail>
            </View>
          }
        </View>
    )
}

const styles = StyleSheet.create({
  baseText: {
    fontSize: 11,
  },
  titleText: {
    fontSize: 15,
    fontWeight: "bold"
  },
  input: {
    height: 40,
    width: 300,
    maxWidth: 500,
    marginLeft: 20,
    fontSize: 16,
    fontStyle: "italic",
    borderWidth: 0,
    borderBottomColor: '#000000',
    borderBottomWidth: 1,
    borderColor: "rgb(201, 201, 201)"
  }
});

export default observer(EvalResults);