import "react-native-gesture-handler";
import { Body, Button, Container, Header, Icon, Left, Right, Spinner, Tab, Tabs } from "native-base";
import React, { useEffect, useState } from "react";
import { SafeAreaProvider } from "react-native-safe-area-context";
import TodayTasksScreen from "components/TodayTasks";
import { ScrollView } from "react-native";
import { Text } from "native-base";
import KidReport from "@components/KidReport";
import {auth} from "firebase/config";
import { useNavigation } from "@react-navigation/native";
import { FontAwesome } from "@expo/vector-icons";
import { useStores } from "../../stores/RootStore";
import { observer } from "mobx-react-lite";
import { runInAction } from 'mobx';

const HelloWorldApp = (props) => {

  // Set an initializing state whilst Firebase connects
  const [initializing, setInitializing] = useState(true);
  const navigation = useNavigation();  
  const  { commonStore } = useStores();

  // Handle user state changes
  const onAuthStateChanged = (user) => {
    if (!user){
      navigation.navigate("LoginScreen");      
    }
    runInAction(()=>{
      commonStore.user = user;
    })
    if (initializing) setInitializing(false);
  }

  useEffect(() => {
    const subscriber = auth.onAuthStateChanged(onAuthStateChanged);
    return subscriber; // unsubscribe on unmount
  }, []);  

  // we will re-render content on tab every time it is active.
  // we could not keep it because the store attached to it will contain data
  // from another user. If we could change store to contain multi user. 
  // that will be acceptable to keep content
  const [tabIndex,setTab] = useState(0);
  const tabChanged = (e)=> {
    console.log(e);
    setTab(e.i);
  };

  const logout = async () => {
    runInAction(()=>{
      commonStore.user = null;
    })
    await auth.signOut();
    navigation.navigate("LoginScreen");
  }

  return (
    <SafeAreaProvider>
        <Container>
          <ScrollView style={{}}>
          <Header>
            <Left>
              <Button transparent>
                <Icon name="menu" />
              </Button>
            </Left>
            <Body>
              <Text style={{fontSize: 20, color: "#fff"}}>SUMO DAYS</Text>
            </Body>
            <Right>
              <Button transparent onPress={logout}>
                <FontAwesome size={24} color="white" name="sign-out" />
              </Button>
            </Right>
          </Header>
          {!commonStore.userName? (<Spinner color="green" />) :
          (commonStore.userRole !== "admin" ? (
          <Tabs onChangeTab={tabChanged}>
            <Tab heading={commonStore.userName}>
              <TodayTasksScreen tabIndex={tabIndex} user={commonStore.userName}/>
            </Tab>
            <Tab heading="Dad">
              <KidReport index={tabIndex}></KidReport>
            </Tab>
          </Tabs>
          ): (
          <Tabs onChangeTab={tabChanged}>
            {
              commonStore.userList.filter(x => x.name != "Dad").map((x,index)=> (
              <Tab key={index} heading={x.name}>
                { index == tabIndex && <TodayTasksScreen tabIndex={tabIndex} user={x.name}/> }
              </Tab>
              ))
            }
            <Tab heading="Dad">
              <KidReport index={tabIndex}></KidReport>
            </Tab>
          </Tabs>)
          )}
          </ScrollView>
        </Container>
    </SafeAreaProvider>
  );
};
const app = observer(HelloWorldApp);
export default app;