import { useNavigation, useRoute, useIsFocused  } from "@react-navigation/native";
import { useStores } from "../stores/RootStore";
import TaskItem from '../models/TaskItem';
import { FontAwesome } from '@expo/vector-icons';
import { TextInput } from 'react-native-gesture-handler';
import React, { useState } from "react";
import { Button, View } from "native-base";
import { Text } from "native-base";
import { StyleSheet } from "react-native";
import { format } from "date-fns";

const CommentList = (props) => {
    
    const [commentValue, setCommentValue] = useState("");
    const {taskStore, storageStore, commonStore} = useStores();

    const taskItem: TaskItem = props.item;

    const addComment = () => {
      taskItem.comments.push({id: Date.now(), text: commonStore.userName + ':' + commentValue});
      setCommentValue("");
      taskStore.saveSelectList(props.user,props.date);
    };

    return (
        <View style={{ width: 400, marginLeft: 20, marginTop: 20, borderWidth: 1, borderColor: "rgb(201, 201, 201)" }}>
          {
              taskItem.comments?.map((cmt,i) => (
                  <Text style={{ fontStyle: "italic", color: i % 2 == 0? "#3962a3": "#39a372" }} key={cmt.id}> {format(new Date(cmt.id),'HH:mm')}: {cmt.text}</Text>
              ))
          }
          <View style={{flexDirection: "row", marginTop: 20}}>
            <TextInput style={styles.input} placeholder="Write to here..." value={commentValue} onChangeText={setCommentValue} ></TextInput>
            <Button style={{marginLeft: 10}} transparent onPress={() => addComment()}>
            <FontAwesome name="send-o" style={{fontSize:25}} color="rgb(63, 81, 181)" />
            </Button>
          </View>
        </View>
    )
}

const styles = StyleSheet.create({
  baseText: {
    fontSize: 11,
  },
  titleText: {
    fontSize: 15,
    fontWeight: "bold"
  },
  input: {
    height: 40,
    width: 300,
    maxWidth: 500,
    marginLeft: 20,
    fontSize: 16,
    fontStyle: "italic",
    borderWidth: 0,
    borderBottomColor: '#000000',
    borderBottomWidth: 1,
    borderColor: "rgb(201, 201, 201)"
  }
});

export default CommentList;