import { addDays } from "date-fns";
import { observer } from "mobx-react-lite";
import { List, ListItem, Spinner } from "native-base";
import { Text } from "native-base";
import React, { useEffect, useState } from "react";
import { Button, ScrollView, StyleSheet, View } from "react-native";
import TaskItem from "../models/TaskItem";
import { useStores } from "../stores/RootStore";


const KidReport = (props) => {
  const [dateSl,setDate] = useState(new Date());
  const { taskStore } = useStores();

  useEffect(()=> {
    const weekFromDate = addDays(dateSl, dateSl.getDay() > 0? 1 -dateSl.getDay(): -6);
    const weekToDate = addDays(dateSl, dateSl.getDay() > 0? 7 - dateSl.getDay(): 0);
    taskStore.loadUserPeriod(["Susu","Nemo"], weekFromDate, weekToDate);
  },[dateSl]);

  const onSelectItem = (task: TaskItem) => {
    task.toggleSelect();
  };

  const SaveSelectList = async() => {
    await taskStore.saveSelectList(props.user,dateSl);
  };

  const onDoneItem = (task: TaskItem) => {
    task.toggleComplete();
  };

  const SelectDay = (val) => {
    setDate(addDays(dateSl, val));
  };

  return (
     <ScrollView>
        <View style={{flexDirection: "column"}}>
          <View style={{flexDirection: "row", justifyContent: "space-around", marginTop: 20, marginBottom: 20 }}>
             <Button style={{size: ""}} title="<< Hôm qua" onPress={()=> SelectDay(-1)}/>
             <Button style={{size: ""}} title="Lưu việc đã chọn" onPress={()=> SaveSelectList()}></Button>
             <Button style={{size: ""}} title="Hôm sau >>" onPress={()=> SelectDay(1)}/>
          </View>
        </View>
        {taskStore.loading ? (<Spinner/> ):
        (
          <View style={{flexDirection: "column"}}>
              {taskStore.periodUserList.map(item => 
                (<View>
                    <Text style={{fontSize: 20}}> {JSON.stringify(item.user)} </Text>
                    <List>
                      {
                        item.dataDates.map(date=>(
                          <ListItem key={date.date + item.user}>
                            <Text> Ngày: {date.date}</Text>
                            <Text> Đăng ký: {date.selected}</Text>
                            <Text style={{fontSize: 18, color: date.selected > date.completed? "red": "black"}}>
                               Hoàn thành: {date.completed}</Text>
                          </ListItem>
                        ))
                      }
                    </List>
                </View>)
              )}
          </View>
        )}
    </ScrollView>
  );
};

const styles = StyleSheet.create({
  result: {
    marginLeft: 10,
    marginBottom: 20,
    fontSize: 18,
    fontWeight: "bold",
    color: "red"
  }
});


const kidreport = observer(KidReport);
export default kidreport;