import { createStackNavigator } from '@react-navigation/stack';
import Home from "components/screens/Home";
import React from "react";
import TaskDetailScreen from "components/screens/TaskDetailScreen";
import LoginScreen from './LoginScreen';

const ModelStack = createStackNavigator();

const ModelStackScreen = ({navigation}) => {
    return (
      <ModelStack.Navigator headerMode="none">
        <ModelStack.Screen name="LoginScreen" component={LoginScreen} />        
        <ModelStack.Screen name="Details" component={TaskDetailScreen} />
      </ModelStack.Navigator>
    );
}

export default ModelStackScreen;