import { addDays, format } from "date-fns";
import orderBy from "lodash/orderBy";
import { action, computed, makeAutoObservable, runInAction } from "mobx";
import { db, storage } from "../firebase/config";
import TaskItem from "../models/TaskItem";
import "../utils/Extensions";

class TaskStore {
  
  taskList = [];
  periodUserList = [];
  loading = false;

  constructor (){
    console.log("Initialize TaskStore...");
    makeAutoObservable(this);
  }

  loadTaskList = async(user, dateSl) => {
    this.loading = true;
    const userDoc = await db.collection("todolist").doc("default").get();    

    const weekFromDate = addDays(dateSl, dateSl.getDay() > 0? 1 -dateSl.getDay(): -6);
    const weekToDate = addDays(dateSl, dateSl.getDay() > 0? 7 - dateSl.getDay(): 0);
    const weekTaskList = await this.loadSelectedDate([user], weekFromDate, weekToDate);

    let selectedTasks: [] = weekTaskList.find( x=> new Date(x.date.seconds*1000).toLocaleDateString() == dateSl.toLocaleDateString())?.selectedTasks;
    if (!selectedTasks){
      const docPath = `${user}_${format(dateSl, "dd-MM-yyyy")}`;
      const taskDoc = await db.collection("selectlist").doc(docPath).get();
      selectedTasks = taskDoc.data()?.selectedTasks;
    }

    if (userDoc.exists && userDoc.data().workList) {
      const listTask = userDoc.data().workList.map((v) => {
        const selectedItem = selectedTasks?.find((x) => v.id == x.taskId);
        const weekCount = weekTaskList.reduce((acc,dayTask) => {
            if (dayTask.selectedTasks.find(x=> x.taskId == v.id)){
                return acc += 1;
            }
            return acc;
        },0);
        return TaskItem.fromTask(
          this,
          v,
          weekCount,
          selectedItem?.attachments,
          selectedItem?.comments,
          selectedItem != null,
          selectedItem?.isCompleted
        );
      });
      runInAction(()=>{
        this.taskList = [];
        this.taskList.push(...listTask);
        this.loading = false;
      })
    } else {
      console.log("No doc values found");
    }
  }

  loadUserPeriod = async (userList, fromDate, toDate) => {
    let userDataList = [];
    this.loading = true;
    const taskList = await this.loadSelectedDate(userList, fromDate, toDate);
    taskList.forEach(item => {
      let dataDate = {
        date: format(new Date(item.date.seconds*1000),'dd-MM-yyyy'),
        selected: item.selectedTasks.length,
        completed: item.selectedTasks.filter( x=>x.isCompleted).length
      };
      const userDt = userDataList.find(x=> x.user == item.user);
      if (!userDt){
        userDataList.push({
          user: item.user,
          dataDates: [dataDate]
        })
      } else {
        userDt.dataDates.push(dataDate);
      }
    });
    runInAction(()=>{
      this.loading = false;
      this.periodUserList = userDataList;
    })
  }

  loadSelectedDate = async (userList, fromDate, toDate) => {
    const dateSelects = [];
    const querySnapshot = await db.collection("selectlist")
      .where("user", "in", userList)
      .where("date", ">=", fromDate)
      .where("date", "<=", toDate)
      .get();
    querySnapshot.forEach((doc) => {
      dateSelects.push(doc.data());
    });
    return dateSelects;
  }

  saveSelectList = async (user, dateSl) => {
    this.loading = true;
    const docPath = `${user}_${format(dateSl, "dd-MM-yyyy")}`;
    const filterTasks = this.taskList
      .filter((x) => x.isSelected)
      .map((task) => ({
        taskId: task.id,
        isCompleted: task.isCompleted,
        comments: task.comments,
        attachments: task.attachments?.map(x=> ({
          ref: x.ref,
          url: x?.url,
          mimeType: x?.mimeType
        }))
      }));
    await db.collection("selectlist").doc(docPath).set({
      date: dateSl,
      lastUpdate: new Date(),
      user,
      selectedTasks: filterTasks,
    });
    runInAction(() => {
      this.loading = false;
    });
  }

  @computed get sortedTaskList() {
    return orderBy(this.taskList, ["isSelected", "id"], ["desc", "asc"]);
  }

  // can be called as `this.total`
  @computed get totalCompleted() {
    return this.taskList.filter((x) => x.isCompleted).length;
  }

  // can be called as `this.total`
  @computed get totalSelected() {
    return this.taskList.filter((x) => x.isSelected).length;
  }

  @action saveSelectTask(id) {

  }

  migrate = async() => {
    db.collection("todolist")
      .doc("default");
  }
}

export default TaskStore;
