import { FontAwesome, FontAwesome5 } from "@expo/vector-icons";
import { Button, View } from "native-base";
import { Text } from "native-base";
import React, { useState } from "react";
import { StyleSheet } from "react-native";
import { TextInput } from "react-native-gesture-handler";
import TransactionDetail from "../models/TransactionDetail";


const EvalDetail = (props) => {

    const MinAwardValue = 10000;
    const [commentValue, setCommentValue] = useState("");
    const [awardValue, setAwardValue] = useState(10000);
    const [iconSize, setIconSize] = useState(30);

    const submitTranDetail = () => {
      const tranDetail: TransactionDetail = new TransactionDetail(Date.now(), getIconName(),iconSize,"Award",awardValue,0,props.date,props.user, [],[commentValue]);
      if (props.onSubmitTranDetail) {
        props.onSubmitTranDetail(tranDetail);
      }
    };

    const getIconName = () => {
      let icon = "";
      if (awardValue > 0) {
        icon = awardValue  <= 10000? "star": (awardValue > 10000 && awardValue < 40000 ? "heart": "medal");
      } else {
        icon = awardValue  >= -10000? "surprise": (awardValue < -10000 && awardValue > -40000? "sad-cry": "angry")
      }
      return icon;
    };

    const changeValue = (delta) => {
        if (awardValue == MinAwardValue && delta < 0) {
          setAwardValue(-10000);
        }
        else if (awardValue == -MinAwardValue && delta > 0) {
          setAwardValue(10000);
        } else {
          if (awardValue > 0 ) {
            setIconSize(iconSize + delta);
          } else {
            setIconSize(iconSize - delta);
          }

          // tăng tốc tiền thường 5 lần
          if (awardValue >= 50000 || awardValue <= -50000) {
            delta = delta * 5;
          }
          setAwardValue(parseInt(awardValue) + delta * 1000);
        }
    };

    return (
        <View style={{ flexDirection: "column" }}>
            <View style={{flexDirection: "row"}}>
              <FontAwesome5 name={getIconName()} style={{fontSize:iconSize, marginLeft: 20}} color={awardValue > 0 ? "rgb(63, 81, 181)": "#786d6c"} />              
              <FontAwesome5 name="plus" style={{fontSize: 24 ,marginLeft: 30}} onPress={e => changeValue(10)} />
              <FontAwesome5 name="minus" style={{fontSize: 24, marginLeft: 10}} onPress={e => changeValue(-10)} />
              <TextInput style={styles.awardValue} placeholder="Giá trị thưởng" value={awardValue.toString()} onChangeText={setAwardValue} ></TextInput>
            </View>            
            <TextInput style={styles.input} placeholder="Thêm nhận xét..." value={commentValue} onChangeText={setCommentValue} ></TextInput>
            <Button style={{margin: 10}} onPress={() => submitTranDetail()}>
                <Text style={{fontSize:16}} > Xác nhận </Text>
            </Button>
        </View>
    );
};

const styles = StyleSheet.create({
  baseText: {
    fontSize: 11,
  },
  titleText: {
    fontSize: 15,
    fontWeight: "bold"
  },
  awardValue: {
    margin: 20,
    height: 20,
    marginTop: 0,
    fontSize: 16,
    fontStyle: "italic",
    borderWidth: 0,
    borderBottomColor: "#000000",
    borderBottomWidth: 1,
    borderColor: "rgb(201, 201, 201)"
  },
  input: {
    height: 40,
    width: 300,
    maxWidth: 500,
    marginLeft: 20,
    fontSize: 16,
    fontStyle: "italic",
    borderWidth: 0,
    borderBottomColor: "#000000",
    borderBottomWidth: 1,
    borderColor: "rgb(201, 201, 201)"
  }
});

export default EvalDetail;