import { computed, makeAutoObservable } from "mobx";

class CommonStore {
  
  user = null;

  userList = [{role: 'child',name: 'Susu', email: 'tulamuaban@gmail.com'},{role: 'child', name: 'Nemo', email: 'dragon.tvq@gmail.com'},{role: 'admin', name: 'Dad', email: 'quyetvv@gmail.com'}]

  constructor (){
    console.log("Initialize CommonStore...");
    makeAutoObservable(this);
  }

  @computed get userName() {    
    return this.userList.find(x=> x.email == this.user?.email)?.name;
  }

  @computed get userRole() {
    return this.userList.find(x=> x.email == this.user?.email)?.role;
  }

  @computed get otherName() {
    return this.userList.find(x=> x.name !== this.userName && x.name != 'Dad')?.name;
  }

}

export default CommonStore;
