import { observable } from "mobx";
import { db } from "../firebase/config";
import "../utils/Extensions";

class SelectTaskStore {
    @observable selectedTasks = [];

    async migrateData(user,date){
        const dateStr = date.format();
        const selectDoc = await db.collection("selectlist" + user).doc(dateStr).get();
        const doneDoc = await db.collection("donelist" + user).doc(dateStr).get();
        let newItems = [];
        let doneTasks  = [];
        const docPath = `${user}_${dateStr}`;
        if (doneDoc.exists && doneDoc.data().doneTasks) {
            doneDoc.data().doneTasks.forEach(taskId => {
              doneTasks.push(taskId);
            });
          }
        if (selectDoc.exists && selectDoc.data().selectedTasks) {
            selectDoc.data().selectedTasks.forEach(taskId => {
                newItems.push({
                    id: `${docPath}_${taskId}`,
                    taskId: taskId,
                    date: date,
                    user,
                    isCompleted: doneTasks.includes(taskId),
                    attachments: []
                });
            });
        }
        db.collection("selectlist").doc(docPath).set({
            lastUpdated: new Date(),
            selectedTasks: newItems
        });
    }
}

export default SelectTaskStore;