import React from 'react';
import TaskStore from './TaskStore';
import SelectTaskStore from './SelectTaskStore';
import StorageStore from './StorageStore';
import CommonStore from './CommonStore';
import MoneyStore from './MoneyStore';

class RootStore {
    taskStore: TaskStore;
    selectTaskStore: ReportTaskStore;
    storageStore: StorageStore;
    commonStore: CommonStore;
    moneyStore: MoneyStore;
    constructor() {
        this.taskStore = new TaskStore(this);
        this.selectTaskStore = new SelectTaskStore(this);
        this.storageStore = new StorageStore(this);
        this.commonStore = new CommonStore(this);
        this.moneyStore = new MoneyStore(this);
    }
}

const rootStore = new RootStore();

export default rootStore;

const storesContext = React.createContext({ ...rootStore });
export const useStores = () => React.useContext(storesContext);

