import { observable, computed, action, makeAutoObservable } from "mobx";
import { TaskStore } from 'stores/TaskStore';

export default class TaskItem {
    taskStore: TaskStore;
    id;
    cat;
    freqPerWeek;
    time;
    task;
    isSelected;
    isCompleted;
    weekCount;
    attachments;
    comments;

    constructor(taskStore, id, task, cat, time, freqPerWeek,weekCount,attachments = [],comments = [],isSelected = false, isCompleted = false) {
		this.taskStore = taskStore;
		this.id = id;
        this.task = task;
        this.cat = cat;
        this.time = time;
		this.freqPerWeek = freqPerWeek;
        this.weekCount = weekCount;
        this.isSelected = isSelected;
        this.isCompleted = isCompleted;
        this.attachments = attachments;
        this.comments = comments;
        makeAutoObservable(this);
	}

    @action toggleSelect = () => {
        this.isSelected = !this.isSelected;
    }

    toggleComplete = () => {
        this.isCompleted = !this.isCompleted;
    }

    static fromTask(taskStore,task,weekCount,attachments = [], comments = [],isSelected = false, isCompleted = false){
        return new TaskItem(taskStore,task.id, task.task, task.cat, task.time, task.freqPerWeek, weekCount,attachments, comments, isSelected, isCompleted);
    }
}