import { FontAwesome } from "@expo/vector-icons";
import { useRoute } from "@react-navigation/native";
import { addDays, format } from "date-fns";
import { observer } from "mobx-react-lite";
import { Body, Button, Card, CardItem, List, Spinner } from "native-base";
import { Text } from "native-base";
import React, { useEffect, useState } from "react";
import { ScrollView, StyleSheet, View } from "react-native";
import TaskItem from "../models/TaskItem";
import { useStores } from "../stores/RootStore";
import EvalResults from "./EvalResults";
import TaskListItem from "./TaskListItem";


const TodayTasksScreen = (props) => {
  const [dateSl,setDate] = useState(new Date());  
  const {taskStore, commonStore, storageStore} = useStores();
  const route = useRoute();
  
  useEffect(()=> {
    taskStore.loadTaskList(props.user,dateSl);
  },[props.tabIndex, props.user,dateSl]);

  const cbAttachAction = ()=> {
    taskStore.saveSelectList(props.user,dateSl);
  }
  useEffect(() => {
    if (route.params?.photo &&  route.params.sourceData?.user == props.user) {
      storageStore.addAttachments(route.params.sourceData?.taskItem, route.params.photo, cbAttachAction);
    }
  }, [route.params?.photo]);

  const onSelectItem = (task: TaskItem) => {
    task.toggleSelect();
    taskStore.saveSelectList(props.user,dateSl);
  };

  const onDoneItem = (task: TaskItem) => {
    task.toggleComplete();
    taskStore.saveSelectList(props.user,dateSl);
  };

  const onAddFile = (e) => {
  };

  const SelectDay = (val) => {
    setDate(addDays(dateSl, val));
  };

  return (
     <ScrollView>
        <View style={{flexDirection: "column"}}>
          <View style={{flexDirection: "row", justifyContent: "space-around", marginTop: 20, marginBottom: 20 }}>
              <Button transparent onPress={()=> SelectDay(-1)}>
                <FontAwesome style={{fontSize:35}} name="backward" color="rgb(33, 150, 243)" />
             </Button>
             <View style={{flexDirection: "row", justifyContent: 'center'}}>
              <Text style={{fontSize: 18,fontWeight: "bold", color: "red"}} > {format(dateSl,"iiii, dd-MM-yyyy")} </Text>
              {taskStore.loading && <Spinner style={{height: 35}}/>}
             </View>
             <Button transparent onPress={()=> SelectDay(1)}>
              <FontAwesome style={{fontSize:35}} name="forward" color="rgb(33, 150, 243)" />
             </Button>
          </View>
        </View>
        <Card>
            <CardItem>
              <Body>
                <EvalResults user={props.user} date={dateSl} tabIndex={props.tabIndex}></EvalResults>
              </Body>
            </CardItem>
        </Card>        
        {taskStore.sortedTaskList.length > 0 &&
        (
          <View style={{flexDirection: "column"}}>
            <Text style={styles.result} >
              {props.user} đã chọn {taskStore.totalSelected} việc. {"\r\n"}Đã hoàn thành: {taskStore.totalCompleted} việc</Text>
            <Text style={{ fontSize: 18, fontWeight: "bold", marginLeft: 15, marginBottom: 20}}>Danh sách công việc: </Text>
            <List>
            {
              taskStore.sortedTaskList.map((taskItem, i) => (
                <TaskListItem key={taskItem.id} taskItem={taskItem} date={dateSl} onSelectItem={onSelectItem} onDoneItem={onDoneItem} onAddFile={onAddFile} {...props} />
              ))
            }
            </List>
          </View>
        )}
    </ScrollView>
  );
};

const styles = StyleSheet.create({  
  result: {
    marginLeft: 10,
    marginBottom: 20,
    fontSize: 22,
    fontWeight: "bold",
    color: "red"
  }
});


const todayScreen = observer(TodayTasksScreen);
export default todayScreen;
