import { createStackNavigator } from '@react-navigation/stack';
import HomeScreen from "@components/screens/HomeScreen";
import LoginScreen from "@components/screens/LoginScreen";
import React from "react";
import TaskDetailScreen from "@components/screens/TaskDetailScreen";
import CameraScreen from '@components/screens/CameraScreen';
import LoadingScreen from './LoadingScreen';

const MainStack = createStackNavigator();

const MainStackScreen = ({navigation}) => {
    return (
      <MainStack.Navigator headerMode="none">
        <MainStack.Screen name="LoadingScreen" component={LoadingScreen} />
        <MainStack.Screen name="HomeScreen" component={HomeScreen} />
        <MainStack.Screen name="LoginScreen" component={LoginScreen} />
        <MainStack.Screen name="CameraScreen" component={CameraScreen} />
        <MainStack.Screen name="TaskDetailScreen" component={TaskDetailScreen} />
      </MainStack.Navigator>
    );
}

export default MainStackScreen;